.nav-container {
    /* position: fixed; */
    display: flex;
    width: 100%;
    height: 7vh;
    align-items: center;
    padding: 0 10%;
    background-color: rgba(255,255,255,0.8);
    border-bottom: 1px solid rgba(0,0,0,0.1);
}

.nav-container .nav-item{
    text-decoration: none;
    color: #6a6a6a;
    
}

.nav-container svg{
    margin-right: 20px;
}

.nav-container .nav-item:nth-child(2){
  margin-right: 20px;
}

.nav-container .nav-item:hover{
    /* box-shadow: 0 1px rgba(0,0,0,0.7); */
}

